import React, { Fragment } from 'react';
import Link from 'next/link';

import Layout from '../Layout';

import { BIO } from '../../strings';
import { splitStringToWords } from '../../utils/anim_utils';
import RubberBand from '../../utils/RubberBand';

import styles from './style.scss';

const words = splitStringToWords(BIO);

const Bio = () => {
  const titleFont = '4rem';
  const paragraphFontSize = '1.5rem';

  return (
    <Layout position="bio">
      <div className={styles.bio_title}>
        <RubberBand fontSize={titleFont}>A</RubberBand>
        <RubberBand fontSize={titleFont}>b</RubberBand>
        <RubberBand fontSize={titleFont}>o</RubberBand>
        <RubberBand fontSize={titleFont}>u</RubberBand>
        <RubberBand fontSize={titleFont}>t</RubberBand>
        <RubberBand fontSize={titleFont}>&nbsp;</RubberBand>
        <RubberBand fontSize={titleFont}>M</RubberBand>
        <RubberBand fontSize={titleFont}>e</RubberBand>
      </div>
      <div className={styles.bio_wrapper}>
        {words.map((el, idx) => {
          return (
            <Fragment key={idx}>
              <RubberBand fontSize={paragraphFontSize}> {el} </RubberBand>
            </Fragment>
          );
        })}
      </div>
    </Layout>
  );
};

export default Bio;
